<template>
  <ul class="total-ul mr20">
    <li v-for="item in timeInterval" :key="item" :class="{'active':  item=== totalTimeInterval}" @click="changeTime(item)">{{item}}</li>
  </ul>
</template>

<script>
export default {
  name: 'clusterSel',
  data () {
    return {
      timeInterval: ['Hourly', 'Daily', 'Weekly', 'Monthly'],
      totalTimeInterval: 'Hourly'
    }
  },
  methods: {
    changeTime (val) {
      this.totalTimeInterval = val || this.totalTimeInterval
      let step = 15
      let startTime = parseInt(new Date().getTime() / 1000)
      let endTime = parseInt(new Date().getTime() / 1000)
      const eachS = 60 * 60
      let x = 0
      let xData = []
      switch (val) {
        case 'Hourly':
          step = 15
          startTime = endTime - eachS
          x = startTime
          while (x < endTime) {
            xData.push(x)
            x += 15 // 1hour / 15s = 4 * 60 = 240
          }
          xData.push(endTime)
          break
        case 'Daily':
          step = 120
          startTime = endTime - (24 * eachS)
          x = startTime
          while (x < endTime) {
            xData.push(x)
            x += 60// 1day / 1min = 24 * 60 = 1440
          }
          xData.push(endTime)
          break
        case 'Weekly':
          step = 1200
          startTime = endTime - (eachS * 7)
          x = startTime
          while (x < endTime) {
            xData.push(x)
            x += 600// 7day / 10min = 7 * 24 * 6 = 1008
          }
          xData.push(endTime)
          break
        case 'Monthly':
          step = 7200
          startTime = endTime - (eachS * 30)
          x = startTime
          while (x < endTime) {
            xData.push(x)
            x += 1800// 1mon / 30min = 30 * 24 * 2 = 1440
          }
          xData.push(endTime)
          break
        default:
          startTime = endTime - eachS
          x = startTime
          while (x < endTime) {
            xData.push(x)
            x += 15 // 1hour / 15s = 4 * 60 = 240
          }
          xData.push(endTime)
      }
      const selVal = {
        startTime: startTime,
        endTime: endTime,
        step: step,
        xData: xData
      }
      this.$parent.timeIntervalChange(selVal)
    }
  },
  mounted () {
    this.changeTime()
  }
}
</script>
<style scoped>
  .total-ul{
    width: auto;
    display: inline-block;
    float: left;
  }
  .total-ul li {
    padding: 8px 20px;
    float: left;
    cursor: pointer;
  }
  .total-ul li.active {
    color: #fff;
    background: #009EFF;
  }
</style>



